
import "./index.scss";
import { Carousel} from 'antd-mobile';
import {history} from "&/history";
import {axios} from "&/axios";
const alert = Modal.alert;
import { Modal } from 'antd-mobile';
import Msgtoggle from "../../mobx";
import {observer} from "mobx-react";
@observer
export default class HouseDetail extends Component{
    constructor(){
        super();
        this.state = {
            houseid:sessionStorage.houseid,
            detail:[],
            banner:[],
            toggle:false,
            order:[],
            topay:false,
            startorder:[],
            endorder:[]
        }
    }
    topay=()=>{
        history.push("/pay");
    }
    tochat=()=>{
        history.push("index/shoujianxiang")
    }
    goback=()=>{
        history.go(-1);
    }
    cancelOrder=()=>{
        if(sessionStorage.se == "start"){
            var newStart = [];
            this.state.startorder.forEach((item,i)=>{
                if(item.houseid != sessionStorage.houseid){
                    newStart.push(item);
                }
            });
            axios.post("/airbnb/payorder",{
                phone:sessionStorage.userPhone,
                startArr:newStart,
                endArr:this.state.endorder
            }).then(res=>{
                this.showAlert();
            })
        }else if(sessionStorage.se == "end"){
            var newEnd = [];
            this.state.endorder.forEach((item,i)=>{
                if(item.houseid != sessionStorage.houseid){
                    newEnd.push(item);
                }
            });
            axios.post("/airbnb/payorder",{
                phone:sessionStorage.userPhone,
                startArr:this.state.startorder,
                endArr:newEnd
            }).then(res=>{
                this.showAlert();
                if(sessionStorage.checked != 0){
                    sessionStorage.checked = sessionStorage.checked*1 - 1;
                }
            })
        }
    }
    showAlert = () => {
        const alertInstance = alert('取消成功', '是否查看其他订单', [
          { text: '返回首页', onPress: () => history.push("/") },
          { text: '查看订单', onPress: () => history.push("/order") }
        ]);
    }
    componentDidMount(){
        scrollTo(0, 0);
        axios.post("/airbnb/housedetail",{
            id:this.state.houseid
        }).then(res=>{
            this.setState({
                detail:res.data.result,
                banner:res.data.result.picture_urls.slice(0,5),
            })
            setTimeout(()=>{
                this.setState({
                    toggle:true
                })
            },500)
        });
        axios.get("/airbnb/order",{
            params:{
                phone:sessionStorage.userPhone
            }
        }).then(res=>{
            this.setState({
                startorder:res.data.result.startorder,
                endorder:res.data.result.endorder
            })
            if(sessionStorage.se == "end"){
                var orders = res.data.result.endorder;
                var order = [];
                orders.forEach((item, i) => {
                    if(item.houseid == sessionStorage.houseid){
                        order.push(item);
                    }
                });
                this.setState({
                    order:order[0],
                    topay:false
                })
            }else if(sessionStorage.se == "start"){
                var orders = res.data.result.startorder;
                var order = [];
                orders.forEach((item, i) => {
                    if(item.houseid == sessionStorage.houseid){
                        order.push(item);
                    }
                });
                this.setState({
                    order:order[0],
                    topay:true
                })

            }
        })
    }
    render(){
        const {
            banner,
            toggle,
            order,
            topay
        } = this.state
        // console.log(this.state.detail.user);
        return(
            <div className="order_detail">
                <div className="house_img">
                    <Carousel
                        autoplay={toggle}
                        autoplayInterval={2000}
                        infinite={true}
                        dotStyle={{ width: '5px', height: '5px', background: '#fff', margin: '0 0.12rem 0.2rem'}}
                        dotActiveStyle={{ background: '#fff', margin: '0 0.12rem 0.2rem'}}
                    >
                        {
                            banner.map((b,i)=>{
                                return(
                                    <img key={i}
                                        src={b}
                                        alt=""
                                        style={{ width: '100%', height: '4.8rem', verticalAlign: 'top' }}
                                        onLoad={() => {
                                        window.dispatchEvent(new Event('resize'));
                                        this.setState({ imgHeight: 'auto' });
                                        }}
                                    />
                                )
                            })
                        }
                    </Carousel>
                    <div className="house_menu">
                        <i className="iconfont icon-back" onClick={this.goback}></i>
                    </div>
                </div>
                <div className="order_body">
                    <div className="order_title">
                        <p>{order.start}-{order.end}</p>
                        <h2>{order.name}</h2>
                    </div>
                    <div className="house_box">
                        <h3>总费用￥{order.price * order.day}</h3>
                        <p>14:00后入住，12:00退房</p>
                    </div>
                    <div className="house_box order_menu" style={{display:topay?"":"none"}} onClick={this.topay}>
                        <div><h3>继续支付</h3></div>
                        <div style={{textAlign: "right",fontSize: "0.42rem",color: "#ccc"}}>〉</div>
                    </div>
                    <div className="house_box order_menu" style={{display:topay?"none":""}} onClick={this.tochat}>
                        <div><h3>查看收件箱</h3></div>
                        <div style={{textAlign: "right",fontSize: "0.42rem",color: "#ccc"}}>〉</div>
                    </div>
                    <div className="house_box order_menu" onClick={this.cancelOrder}>
                        <div><h3>取消预订</h3></div>
                        <div style={{textAlign: "right",fontSize: "0.42rem",color: "#ccc"}}>〉</div>
                    </div>
                </div>
            </div>
        )
    }
}